import React, { useState } from 'react';

import TabBtn from '../../components/TabBtn';
import {
  information,
} from './datas';
import styles from './index.less';
import ActiveInfo from '../../../../components/ActiveInfo';
import Box from '../../Boxs/Box';
import { UserState } from '../../model';
import { connect } from 'dva';

interface IProps {
  user: UserState;
}

const BasicData = ({ data }) => {
  return (
    <Box title="">
      <div>
        <ActiveInfo title="" value={data?.wealthAccount?.isRegister ? '' : ''} />
        <ActiveInfo title="" value={data?.wealthAccount?.isAccount ? '' : ''} />
        <ActiveInfo title="" value={data?.wealthRecord?.isBindWeChat} />
        <ActiveInfo title="" value={data?.wealthRecord?.addTime} />
        <ActiveInfo title="" value={data?.wealthRecord?.phone} />
        <ActiveInfo title="" value={data?.wealthRecord?.age} />
        <ActiveInfo title="" value={data?.wealthRecord?.isBindWeChat} />
        <ActiveInfo title="500" value={data?.wealthStatus?.isSpxClient} />
        <ActiveInfo title="" value={data?.wealthStatus?.isAnnuityClient} />
        <ActiveInfo title="wealth" value={data?.wealthStatus?.isWealthClient} />
        <ActiveInfo title="" value='' />
      </div>
    </Box>
  )
}

const PlanInvestment = ({ data }) => {
  return (
    <Box title="">
      <div>
        <ActiveInfo title="" value={data?.wealthRecord?.walletAmount} />
        <ActiveInfo title="" value={data?.wealthRecord?.best3CoinPoAmount} />
        <ActiveInfo title="" value={data?.wealthRecord?.myPoAmount} />
        <ActiveInfo title="" value={data?.wealthRecord?.happyPoAmount} />
        <ActiveInfo title="" value={data?.wealthRecord?.flowerFruitPoAmount} />
        <ActiveInfo title="" value={data?.wealthRecord?.securitiesAmount} />
        <ActiveInfo title="" value={data?.wealthRecord?.shortDebtPoAmount} />
        <ActiveInfo title="" value={data?.wealthRecord?.fundAmount} />
      </div>
    </Box>
  )
}

const DataTabs: React.FC<IProps> = (props) => {
  const { user: { userData } } = props;
  console.log(props);


  const [type, setType] = useState<string>('');

  const handleClick = (value: string) => {
    setType(value)
  }

  return (
    <>
      <div className={styles.headerBox}>
        <TabBtn options={information} onChange={handleClick} />
      </div>
      {
        type ? type === 'basic_data' && <div className={styles.contentBox}>
          <BasicData data={userData.wealth} />
        </div> : <div className={styles.contentBox}>
            <BasicData data={userData.wealth} />
          </div>
      }
      {
        type ? type === 'plan_investment' && <div className={styles.contentBox}>
          <PlanInvestment data={userData.wealth} />
        </div> : <div className={styles.contentBox}>
            <PlanInvestment data={userData.wealth} />
          </div>
      }
    </>
  )
}
export default connect(({ user }: { user: UserState }) => ({
  user,
}))(DataTabs);