import React, { useEffect, useState } from 'react';
import { connect } from 'dva';

import { fetchUpdateInfo } from '../../../../services/detail';
import { DecryptDes, GetCookie } from '../../../../utils/func';
import { UserState } from '../../model';
import ActiveInfo from '../../../../components/ActiveInfo';
import EditorInput from '../../../../components/EditorInput';
import styles from '../index.less';

interface WhiteInformationProps {
  user: UserState;
}

const WhiteInformation: React.FC<WhiteInformationProps> = (props) => {
  const { user } = props;
  const { userData } = user;
  const [porscheIsWinner, setPorscheIsWinner] = useState<string>('');
  const [porscheHaveAddress, setPorscheHaveAddress] = useState<string>('');
  const [porscheMemo, setPorscheMemo] = useState<string>('');

  const handlePorscheIsWinner = (val: string) => {
    // 
    setPorscheIsWinner(val)
    fetchUpdateInfo({
      externalUserId: DecryptDes(GetCookie('externaluserid')),
      porscheIsWinner: '',
    })
  }

  const handlePorscheHaveAddress = (val: string) => {
    // 
    setPorscheHaveAddress(val)
  }


  useEffect(() => {

  }, [user])

  return (
    <div>
      <div className={styles.contentBox}>
        <div style={{ padding: '0 20px' }}>
          <ActiveInfo title="" value={userData?.channel} />
          <ActiveInfo title="" value={userData?.addTime} />
          <ActiveInfo title="" value={userData?.pro?.trainingData?.phone} />
          <ActiveInfo title="" value={userData?.family?.age} />
          <ActiveInfo title="" value={userData?.subscribe} alignItems="flex-start" />
          <ActiveInfo title="" value={userData?.feimooc?.learn ? '' : ''} />
          <ActiveInfo title="" value={userData?.feimooc?.userInfo} />
          <ActiveInfo title="" value={userData?.feimooc?.inFirstInsure ? '' : ''} />
          <ActiveInfo title="" value={userData?.train?.consultantCnName} />
          <ActiveInfo title="" value={userData?.feimooc?.fixedInvestmentCamp ? '' : ''} />
          <ActiveInfo title="" value={
            <div>
              <EditorInput value={porscheIsWinner || userData?.customerInfo?.porscheIsWinner} onChange={handlePorscheIsWinner} />
            </div>
          } />
          <ActiveInfo title="" value={
            <div>
              <EditorInput value={porscheHaveAddress || userData?.customerInfo?.porscheHaveAddress} onChange={handlePorscheHaveAddress} />
            </div>
          } />
          <ActiveInfo title="" value={
            <div>
              <EditorInput value={porscheMemo || userData?.customerInfo?.porscheMemo} />
            </div>
          } />
        </div>
      </div>
    </div>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(WhiteInformation);