import React, { useEffect } from 'react';

import { fetchHealth } from '../../../services/detail';
import { DecryptDes, GetCookie } from '../../../utils/func';
import IndexContentContext from '../IndexContentContext';
import Box from './Box';
import ActiveInfo from '../../../components/ActiveInfo';
import edit from '../../../assets/icons/edit.svg';
import styles from './index.less';

const CardBox: React.SFC = () => {

  return (
    <div className={styles.trainWealthQuestionCard}>
      <img src="http://wework.qpic.cn/bizmail/iajiakxs31goYHIFkIRm7g3GaniaH878bwcZmIiaXFIxYibBTic7GJGExDSw/0" alt="" className={styles.left} />
      <div className={styles.right}>
        <p> | <span>1998-03-15</span></p>
        <ul>
          <li></li>
        </ul>
      </div>
    </div>
  )
}

const TrainWealthQuestion: React.SFC = () => {
  useEffect(() => {
    fetchHealth({ unionid: DecryptDes(GetCookie('unionid')) })
  }, [])

  return (
    <IndexContentContext.Consumer>
      {context => {
        return (
          <Box title="">
            <div>
              <ActiveInfo title="" value={
                <div onClick={() => context.openRemark('train_wealth_question')}><img src={edit} alt="" /></div>
              } />
              <CardBox />
            </div>
          </Box>
        )
      }}
    </IndexContentContext.Consumer>
  )
}

export default TrainWealthQuestion;