import React, { useState } from 'react';
import { connect } from 'dva';

import { fetchUpdateHealthRemark } from '../../../../services/detail';
import { GlobalState } from '../../../../models/global';
import Mask from '../../../../components/Mask';
import Textarea from '../../../../components/Textarea';
import Button from '../../../../components/Button';

interface RemarkProps {
  visible: boolean;
  onCancel: Function;
  type: string;
  global: GlobalState;
}

const Remark: React.FC<RemarkProps> = (props) => {
  const { visible, onCancel, type, global } = props;
  const { externaluserid } = global;

  const [value, setValue] = useState<string>('');

  const handleSubmit = () => {
    console.log(type)
    if (type === 'train_wealth_question') {
      // 
      fetchUpdateHealthRemark({
        unionId: externaluserid,
        healthRemark: value
      })
    }

    onCancel();
  }

  return (
    <Mask title="" position="middle" visible={visible} onCancel={onCancel} footer={
      <div style={{display: 'flex', justifyContent: 'flex-end', paddingRight: '10px'}}>
        <Button size="small" style={{marginRight: '5px', marginTop: '0px'}}></Button>
        <Button type="primary" size="small" style={{marginTop: '0px'}} onClick={handleSubmit}></Button>
      </div>
    }>
      <div style={{margin: '10px', height: '100px'}}>
        <Textarea onChange={(val) => setValue(val)}></Textarea>
      </div>
    </Mask>
  )
}

export default connect(({ global }: { global: GlobalState }) => ({
  global,
}))(Remark);