import React, { ReactNode, useState } from 'react';

import { isMobile } from '../../utils/func';
import styles from './index.less';

interface TooltipProps {
  children: ReactNode;
  title: string;
}

const Tooltip: React.SFC<TooltipProps> = (props) => {
  const { children, title } = props;
  const [visible, setVisible] = useState<boolean>(false);

  const handleMouseEnter = () => {
    // 
    if (!isMobile()) {
      setVisible(true)
    }
  }

  const handleMouseLeave = () => {
    // 
    if (!isMobile()) {
      setVisible(false)
    }
  }

  return (
    <div className={styles['pulan_toopltip']}>
      <div className={styles['pulan_toopltip_tips'] + ' ' + styles['pulan_toopltip_tips_top']} style={{opacity: visible ? 1 : 0}}>
        {title}
      </div>
      <div onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
        {children}
      </div>
    </div>
  )
}

export default Tooltip;