import React from 'react';

import IndexContentContext from './IndexContentContext';
import Train from './Tabs/Train';
import Wealth from './Tabs/Wealth';
import White from './Tabs/White';
import Planpro from './Tabs/Planpro';
import styles from './index.less';

const tabs = {
  train: [
    { title: '', sub: 'information' },
    { title: '', sub: 'followUp' },
    { title: '', sub: 'explanation' },
    { title: '', sub: 'news' }
  ],
  planpro: [
    { title: '', sub: 'information' },
    { title: '', sub: 'news' }
  ],
  white: [
    { title: '', sub: 'information' },
    { title: '', sub: 'news' }
  ],
  wealth: [
    { title: '', sub: 'information' },
    { title: 'Wealth', sub: 'server' },
    { title: '', sub: 'risk' },
    { title: '', sub: 'news' }
  ]
}

const DetailTabs: React.FC = () => {
  return (
    <IndexContentContext.Consumer>
      {context => {
        const { type } = context;

        return (
          <div className={styles.detailTabsContainer}>
            {
              type === 'train' && <Train options={tabs[type]} />
            }
            {
              type === 'white' && <White options={tabs[type]} />
            }
            {
              type === 'planpro' && <Planpro options={tabs[type]} />
            }
            {
              type === 'wealth' && <Wealth options={tabs[type]} />
            }
          </div>
        )
      }}
    </IndexContentContext.Consumer>
  )
}

export default DetailTabs;