import React, { useEffect, useState } from 'react';

import TabBtn from '../../components/TabBtn';
import {
  Planpro_information,
} from './data';
import styles from './index.less';
import ActiveInfo from '../../../../components/ActiveInfo';
import Box from '../../Boxs/Box';
import { UserState } from '../../model';
import { connect } from 'dva';

import edit from '../../../../assets/icons/edit.svg';
import { DecryptDes, GetCookie } from '../../../../utils/func';

interface IProps {
  user: UserState;
  dispatch: Function,
}

const BasicData = ({ data }) => {
  return (
    <Box title="">
      <div>
        <ActiveInfo title="" value={data?.channel} />
        <ActiveInfo title="" value={data?.addTime} />
        <ActiveInfo title="" value={data?.feimooc.signupPhone} />
        <ActiveInfo title="" value={data?.subscribe} />
        <ActiveInfo title="" value={data?.description} />
        <ActiveInfo title="" value={data?.feimooc.inFirstInsure ? '' : data?.feimooc.fixedInvestmentCamp ? '' : ''} />
        <ActiveInfo title="" value={data?.pro.city} />
        <ActiveInfo title="" value={data?.pro.age} />
        <ActiveInfo title="" value={data?.pro.maritalStatus} />
        <ActiveInfo title="" value={data?.pro.childNum} />
        <ActiveInfo title="" value={data?.pro.income} />
        <ActiveInfo title="" value={data?.pro.AnnualAssetsBegin} />
        <ActiveInfo title="" value={data?.pro.currentAssetsBegin} />
        <ActiveInfo title="" value={data?.pro.staffCate} />
        <ActiveInfo title="FYP" value={data?.pro.estimatedFYP} />
        <ActiveInfo title="" value={data?.pro.policyAmount} />
        <ActiveInfo title="PlanPro" value={data?.pro.policyNum} />
        <ActiveInfo title="" value={data?.pro.source} />
        <ActiveInfo title="" value={data?.pro.matching} />
      </div>
    </Box>
  )
}

const HealthQuestionnaire = () => {
  return (
    <Box title="">
      <div>
        <ActiveInfo title="" value={
          <div><img src={edit} alt="" /></div>
        } />
        <div className={styles.trainWealthQuestionCard}>
          <img src="http://wework.qpic.cn/bizmail/iajiakxs31goYHIFkIRm7g3GaniaH878bwcZmIiaXFIxYibBTic7GJGExDSw/0" alt="" className={styles.left} />
          <div className={styles.right}>
            <p> | <span>1998-03-15</span></p>
            <ul>
              <li></li>
            </ul>
          </div>
        </div>
      </div>
    </Box>
  )
}

const SecurityPlan = ({ data }) => {
  return (
    <Box title="">
      <div>
        <ActiveInfo title="" value={data?.pro.baoConsultantCnName} />
        <ActiveInfo title="" value={data?.pro.GuaranteeData.ServiceSchedule} />
        <ActiveInfo title="" value={data?.pro.GuaranteeData.silence} />
        <ActiveInfo title="" value={data?.pro.GuaranteeData.visit_status} />
      </div>
    </Box>
  )
}

const ComprehensivePlan = ({ data }) => {
  return (
    <Box title="">
      <div>
        <ActiveInfo title="" value={data?.pro.consultantCnName} />
        <ActiveInfo title="" value={data?.pro.FinanceData.ServiceSchedule} />
        <ActiveInfo title="" value={data?.pro.FinanceData.ServiceSchedule} />
        <ActiveInfo title="" value={data?.pro.FinanceData.visit_status} />
      </div>
    </Box>
  )
}

const PlanproDataTabs: React.FC<IProps> = (props) => {
  const { dispatch, user: { userData } } = props;
  const [type, setType] = useState<string>('');

  useEffect(() => {
    dispatch({
      type: 'user/fetchHealth',
      payload: {
        unionid: DecryptDes(GetCookie('unionid')),
      }
    }).then(res => {
      console.log(res);

    })
  }, [])

  const handleClick = (value: string) => {
    setType(value)
  }

  return (
    <>
      <div className={styles.headerBox}>
        <TabBtn options={Planpro_information} onChange={handleClick} />
      </div>
      {
        type ? type === 'basic_data' && <div className={styles.contentBox}>
          <BasicData data={userData} />
        </div> : <div className={styles.contentBox}>
            <BasicData data={userData} />
          </div>
      }
      {
        type ? type === 'health_questionnaire' && <div className={styles.contentBox}>
          <HealthQuestionnaire />
        </div> : <div className={styles.contentBox}>
            <HealthQuestionnaire />
          </div>
      }
      {
        type ? type === 'security_plan' && <div className={styles.contentBox}>
          <SecurityPlan data={userData} />
        </div> : <div className={styles.contentBox}>
            <SecurityPlan data={userData} />
          </div>
      }
      {
        type ? type === 'comprehensive_plan' && <div className={styles.contentBox}>
          <ComprehensivePlan data={userData} />
        </div> : <div className={styles.contentBox}>
            <ComprehensivePlan data={userData} />
          </div>
      }
    </>
  )
}
export default connect(({ user }: { user: UserState }) => ({
  user,
}))(PlanproDataTabs);