import React from 'react';
import { Tabs } from 'antd-mobile';

import PlanproDynamic from './PlanproDynamic';
import IndexContentContext from '../../IndexContentContext';
import PlanproDataTabs from './PlanproDataTabs';

import styles from './index.less';

interface TrainProps {
  options: any[];
}

const Teacher: React.FC<TrainProps> = (props) => {
  const { options } = props;

  return (
    <IndexContentContext.Consumer>
      {context => {
        return (
          <div className={styles.detailTabsContainer}>
            <Tabs tabs={options}>
              {/**  */}
              <PlanproDataTabs />
              {/**  */}
              <PlanproDynamic />
            </Tabs>
          </div>
        )
      }}
    </IndexContentContext.Consumer>
  )
}

export default Teacher;