import React from 'react';
import { Tabs } from 'antd-mobile';

import WhiteDynamic from './WhiteDynamic';
import IndexContentContext from '../../IndexContentContext';
import WhiteInformation from './WhiteInformation';

import styles from './index.less';

interface TrainProps {
  options: any[];
}

const Teacher: React.FC<TrainProps> = (props) => {
  const { options } = props;

  return (
    <IndexContentContext.Consumer>
      {context => {
        return (
          <div className={styles.detailTabsContainer}>
            <Tabs tabs={options}>
              {/**  */}
              <WhiteInformation />
              {/**  */}
              <WhiteDynamic />
            </Tabs>
          </div>
        )
      }}
    </IndexContentContext.Consumer>
  )
}

export default Teacher;