import React, { useState } from 'react';

import TabBtn from '../../components/TabBtn';
import ActiveInfo from '../../../../components/ActiveInfo';
import {
  server,
} from './datas';
import styles from './index.less';
import Box from '../../Boxs/Box';
import { connect } from 'dva';
import { UserState } from '../../model';

const Solid = ({ data }) => {
  return (
    <Box title="Wealth">
      <div>
        <ActiveInfo title="" value={data?.wealthService?.city} />
        <ActiveInfo title="" value={data?.wealthService?.birth} />
        <ActiveInfo title="" value={data?.wealthService?.totalIncome} />
        <ActiveInfo title="" value={data?.wealthService?.rest} />
        <ActiveInfo title="" value={data?.wealthService?.floatAsset} />
        <ActiveInfo title="" value={data?.wealthService?.floatAsset} />
        <ActiveInfo title="" value={data?.wealthService?.childNum} />
        <ActiveInfo title="" value={data?.wealthService?.userName} />
        <ActiveInfo title="" value={data?.wealthService?.createDate} />
        <ActiveInfo title="" value='' />
      </div>
    </Box>
  )
}

const FamilyTarget = ({ data }) => {
  return (
    <Box title="">
      <div className={styles.trainWealthQuestionCard}>
        <img src="http://wework.qpic.cn/bizmail/iajiakxs31goYHIFkIRm7g3GaniaH878bwcZmIiaXFIxYibBTic7GJGExDSw/0" alt="" className={styles.left} />
        <div className={styles.right}>
          <div> | <span>65</span></div>
          <div>1998-03-15</div>
        </div>
      </div>
      <div className={styles['family-target-bg']}>
        <ActiveInfo title="" value="" />
        <ActiveInfo title="" value="" />
        <ActiveInfo title="" value="" />
      </div>
    </Box>
  )
}
interface IProps {
  user: UserState;
}

const ActiviteFollowUp: React.FC<IProps> = (props) => {
  const { user: { userData } } = props;
  const [type, setType] = useState<string>('');

  const handleClick = (value: string) => {
    console.log(value)
    setType(value)
  }

  return (
    <div>
      <div className={styles.headerBox}>
        <TabBtn options={server} onChange={handleClick} />
      </div>
      {
        type ? type === 'solid' && <div className={styles.contentBox}>
          <Solid data={userData.wealth} />
        </div> : <div className={styles.contentBox}>
            <Solid data={userData.wealth} />
          </div>
      }
      {
        type ? type === 'family_target' && <div className={styles.contentBox}>
          <FamilyTarget data={userData.wealth} />
        </div> : <div className={styles.contentBox}>
            <FamilyTarget data={userData.wealth} />
          </div>
      }
    </div>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(ActiviteFollowUp);