import React, { useEffect, useState } from 'react';
import { connect } from 'dva';

import { fetchGetCustomerChatEventList } from '../../../../services/detail';
import { ActiviCommunicationState } from '../../../datas';
import { GlobalState } from '../../../../models/global';
import {
  Train_follow_up,
} from '../../../datas';
import TabBtn from '../../components/TabBtn';
import ActiveInfo from '../../../../components/ActiveInfo';
import Empty from '../../components/Empty';
import styles from '../index.less';

interface TrainActiviteFollowUpProps {
  global: GlobalState;
  history: any,
}

interface ListsProps {
  chatEventId: number;
  communicationState: number;
  nextCommunicationDate: string;
  startTime: string;
  endTime: string;
  estimatedFyp: number;
  mainObjection: string;
  type: number;
  fyp: number;
}

const TrainActiviteFollowUp: React.SFC<TrainActiviteFollowUpProps> = (props) => {
  const { global, history } = props;
  const { externaluserid } = global;
  const [type, setType] = useState<number>(1);
  const [datas, setDatas] = useState<ListsProps | null>(null);
  const [lists, setLists] = useState<ListsProps[]>([]);

  const handleClick = (value: number) => {
    console.log(value)
    setType(value)
    lists.forEach((i: any) => {
      if (i.type === value) {
        setDatas(i);
        return;
      }
    })
  }

  useEffect(() => {
    if (externaluserid) {
      fetchGetCustomerChatEventList(externaluserid).then((res: any) => {
        if (res.code === 200 && res.data) {
          res.data.forEach((i: any) => {
            if (i.type === type) {
              setDatas(i);
              return;
            }
          })
        }
      })
    }
  }, [global])

  return (
    <div>
      <div className={styles.headerBox}>
        <TabBtn options={Train_follow_up} onChange={handleClick}/>
      </div>
      <div className={styles.contentBox}>
        <div style={{padding: '0 15px'}}>
          {
            datas && 
            <>
              <ActiveInfo title="" value="" />
              <ActiveInfo title="" value={ActiviCommunicationState[datas.communicationState]} />
              <ActiveInfo title="" value={datas.nextCommunicationDate} />
              <ActiveInfo title="FYP" value={`${datas.estimatedFyp}`} />
              <ActiveInfo title="" value={datas.mainObjection} />
              <ActiveInfo title="FYP" value={datas.fyp} />
              <ActiveInfo title="" value={
                <span style={{color: '#0681F4'}} onClick={()=>history.push(`/followUp?type=activity&chatEventId=${datas.chatEventId}`)}></span>
              }/>
            </>  
          }
          {
            !datas && <Empty content="" />
          }
        </div>
      </div>
    </div>
  )
}

export default connect(({ global }: { global: GlobalState }) => ({
  global,
}))(TrainActiviteFollowUp);