import React, { useEffect, useState } from 'react';
import { connect } from 'dva';

import { UserState } from '../model';
import Box from './Box';
import ActiveInfo from '../../../components/ActiveInfo';
import send_plane from '../../../assets/icons/send_plane.svg';

interface TrainBasicDataProps {
  user: UserState
}

const TrainBasicData: React.SFC<TrainBasicDataProps> = (props) => {
  const { user } = props;
  const { userData } = user;
  const [curriculums, setCurriculums] = useState<string[]>([]);

  const handleSendWealth = () => {
    if (wx) {
      let  shareConfig = {
        msgtype: "news",
        news: {
          link: userData.smartUrl.link, //H5url 
          title: userData.smartUrl.title, //H5
          desc: userData.smartUrl.desc, //H5
          imgUrl: userData.smartUrl.imgUrl, //H5URL
        }
      }
      wx.invoke('sendChatMessage', shareConfig, function (res) {
        if (res.err_msg == 'sendChatMessage:ok') {
            //
        } else {
            console.log(res.err_msg)
        }
      })
    }
  }

  useEffect(() => {
    if (userData) {
      const learns = [];

      userData.feimooc.learn.planCamp.forEach((i: any) => {
        if (!learns.includes(i.title)) {
          learns.push(i.title)
        }
      })

      userData.feimooc.learn.xbCamp.forEach((i: any) => {
        if (!learns.includes(i.title)) {
          learns.push(i.title)
        }
      })

      console.log(learns)
      setCurriculums(learns);
    }
  }, [global])

  return (
    <Box title="">
      <div>
        <ActiveInfo title="" value={userData?.channel} alignItems="flex-start" />
        <ActiveInfo title="" value={userData?.addTime} />
        <ActiveInfo title="" value={userData?.pro?.trainingData?.phone} />
        <ActiveInfo title="" value={userData?.subscribe} alignItems="flex-start" />
        <ActiveInfo title="" value={userData?.description} />
        <ActiveInfo title="" alignItems="flex-start" value={curriculums.map((i: string, n: number) => {
          if (n === curriculums.length - 1) {
            return i
          } else {
            return `${i}`
          }
        })} />
        <ActiveInfo title="" value={userData?.train?.training_name} />
        <ActiveInfo title="" value={`${(userData?.train?.training_start_time)?.substring(0, 4)}-${(userData?.train?.training_start_time)?.substring(4, 6)}-${(userData?.train?.training_start_time)?.substring(6, 8)}`} />
        <ActiveInfo title="" value={userData?.train?.consultantCnName} />
        <ActiveInfo title="" value={userData?.customerInfo.porscheMemo} />
        <ActiveInfo title="" value={
        <div style={{ display: 'flex', alignItems: 'center', fontWeight: 400, color: '#1890FF' }} onClick={handleSendWealth}>
          <img src={send_plane} alt="" style={{ marginLeft: '5px' }} />
        </div>} />
        <ActiveInfo title="" value="" />
      </div>
    </Box>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(TrainBasicData);