import React, { useEffect, useState } from 'react';
import moment from 'moment';

import { fetchComplete } from '../../services/detail';
import Box from './Boxs/Box';
import Checkbox from '../../components/Checkbox';
import clock from '../../assets/icons/clock.svg';
import styles from './index.less';

interface DataSource {
  msg: string; // 
  todo_time: number; // 
  remind_time: number; // 
  remind_status: number; // 
  id: number;
  complete_status: number; // 
}

interface DealtCardProps {
  history: any;
  dataSource: DataSource;
  onComplete?: Function;
}

const DealtCard: React.FC<DealtCardProps> = (props) => {
  const { history, dataSource, onComplete } = props;
  const [hourInterval, setHourInterval] = useState<number>(0);
  const [minuteInterval, setMinuteInterval] = useState<number>(0);
  const [dayInterval, setDayInterval] = useState<number>(0);
  const [isDone, setIsDone] = useState<boolean>(false);

  const handleSubmit = (val: boolean) => {
    setIsDone(val);
    fetchComplete({
      id: dataSource.id,
      completeStatus: val ? 1 : 0
    }).then((res: any) => {
      if (res.code === 200) {
        onComplete();
      }
    })
  }

  useEffect(() => {
    let day = (dataSource.remind_time * 1000 - new Date().getTime()) / 86400000;
    setDayInterval(Math.floor(day));
    let hour = (dataSource.remind_time * 1000 - new Date().getTime()) * 24 / 86400000;
    setHourInterval(Math.floor(hour % 24));
    let minute = (dataSource.remind_time * 1000 - new Date().getTime()) * 24 * 60 / 86400000;
    setMinuteInterval(Math.floor(minute % 60));
  }, [])

  return (
    <div style={{ margin: '10px 0' }}>
      <Box title="" extra={<span style={{ color: '#1890FF' }} onClick={() => history.push('/backlog')} ></span>}>
        <div className={styles.dealtCardItem} style={isDone ? { background: '#F5F5F5', padding: '10px' } : {}}>
          <div style={{ paddingTop: '3px' }}>
            <Checkbox onChange={handleSubmit} />
          </div>
          <div className={styles.right}>
            <p className={styles.line1}>{dataSource.msg || ''}</p>
            <p className={styles.line2}>
              <img src={clock} alt="time" /> {moment(dataSource.todo_time * 1000).format('HH:mm') || '--'}
            </p>
            <p className={styles.line3}>{dayInterval}{hourInterval}{minuteInterval}</p>
          </div>
        </div>
      </Box>
    </div>
  )
}

export default DealtCard;