import React, { useEffect } from 'react';
import { connect } from 'dva';

import { fetchCallList } from '../../../services/detail';
import { UserState } from '../model';
import { CommunicationStateType, ExecutionStatusType, RefundPolicyType } from '../../datas';
import Box from './Box';
import ActiveInfo from '../../../components/ActiveInfo';

interface TrainInfomationProps {
  user: UserState
}

const TrainInfomation: React.SFC<TrainInfomationProps> = (props) => {
  const { user } = props;
  const { userData } = user;

  useEffect(() => {
    if (userData) {
      console.log(userData.train.policyCount)
    }
  }, [user])

  
  return (
    <Box title="">
      <div>
        <ActiveInfo title="" value={CommunicationStateType[userData?.train?.planInfo?.communication_state]} />
        <ActiveInfo title="" value={`${(userData?.train?.planInfo?.nexttime)?.substring(0, 4)}/${(userData?.train?.planInfo?.nexttime)?.substring(4, 6)}/${(userData?.train?.planInfo?.nexttime)?.substring(6, 8)} ${(userData?.train?.planInfo?.nextClock)?.substring(0, 5)}-${(userData?.train?.planInfo?.nextEndClock)?.substring(0, 5)}`} />
        <ActiveInfo title="FYP" value={`${userData?.train?.planInfo?.estimatedFYP}`} />
        <ActiveInfo title="" value={`${userData?.train?.latest_remark}`} />
        <ActiveInfo title="" value={`${ExecutionStatusType[userData?.train?.execution_status]}`} />
        <ActiveInfo title="" value={`${userData ? userData.train.policyCount : 10}`} />
        <ActiveInfo title="FYP" value={`${userData?.train?.fyp}`} />
        <ActiveInfo title="" value={`${RefundPolicyType[userData?.train?.refund_policy]}`} />
        <ActiveInfo title="Call caption" value={<span style={{ color: '#40A3FF' }}>YES</span>} />
      </div>
    </Box>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(TrainInfomation);